___CSS_LOADER_EXPORT___.push([module.id, `body {
    background-color: #eff1ee;
    font-family: Arial, Helvetica, sans-serif;
    display: flex;
    justify-content: center;
    padding: 0;
    margin: 0;
    height: 100vh;
}

#root {
    width: 100%;
    padding: 1.5em;
}

nav {
    display: flex;
    justify-content: space-between;
}

.account-links {
    width: 10rem;
    display: flex;
    justify-content: space-evenly;
}
#signout-btn:hover {
    text-decoration: underline;
    cursor: pointer;
}

#main-container {
    border: 1px solid black;
    display: flex;
    justify-content: space-between;
    align-content: stretch;
    height: 90%;
}

.container {
    border: 1px solid black;
    background-color: #9ec1d7;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

#map-container {
    border: 1px solid black;
    width: 100%;
    height: 100%;
}

/* #map-container img {
    
} */

#loc-search-container {
    width: 80%;
    display: flex;
    justify-content: center;
}

#location-combobox {
    font-size: 20px;
    padding: 1.5rem;
    margin: 0.5rem;
    width: 20vw;
    height: 0.5em;
    border-radius: 1rem;
}
#location-combobox:focus {
    outline: none;
}

#loc-submit-btn {
    height: 2em;
    margin: 0.5rem;
    padding: 0.75rem;
    font-size: 28px;
    border-radius: 0.5rem;
}

#loc-list {
    background-color: #18465a;
    width: 95%;
    border-radius: 0.5em;
    padding: 0.25rem;
    max-height: 65%;
    overflow-y: scroll;
}
#loc-submit-btn::-webkit-scrollbar {
    display: none;
}
/* #loc-submit-btn {
    -ms-overflow-style: none;
    scrollbar-width: none;
} */

.list-item {
    background-color: #efeded;
    width: 95%;
    border: 0.5px solid;
    border-radius: 0.25em;
    padding: 0.25rem;
    display: grid;
    grid-template-columns: 50% 25% 25%;
    justify-content: space-between;
    padding: 0.5em;
}

#earth {
    width: 300px;
    min-width: 300px;
    height: 300px;
    min-height: 300px;
    background: url(https://web.archive.org/web/20150807125159if_/http://www.noirextreme.com/digital/Earth-Color4096.jpg);
    border-radius: 50%;
    background-size: 610px;
    box-shadow: inset 8px 36px 80px 36px rgb(0, 0, 0), inset -6px 0 12px 4px rgba(255, 255, 255, 0.3);
    animation-name: rotate;
    animation-duration: 12s;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
    -webkit-animation-name: rotate;
       -webkit-animation-duration: 12s;
       -webkit-animation-iteration-count: infinite;
       -webkit-animation-timing-function: linear;
  }
  @keyframes rotate {
    from {
      background-position: 0px 0px;
    }
    to {
      background-position: 610px 0px;
    }
  }
  @-webkit-keyframes rotate {
    from {
      background-position: 0px 0px;
    }
    to {
      background-position: 610px 0px;
    }
  }`, "",{"version":3,"sources":["webpack://./client/app.css"],"names":[],"mappings":"AAAA;IACI,yBAAyB;IACzB,yCAAyC;IACzC,aAAa;IACb,uBAAuB;IACvB,UAAU;IACV,SAAS;IACT,aAAa;AACjB;;AAEA;IACI,WAAW;IACX,cAAc;AAClB;;AAEA;IACI,aAAa;IACb,8BAA8B;AAClC;;AAEA;IACI,YAAY;IACZ,aAAa;IACb,6BAA6B;AACjC;AACA;IACI,0BAA0B;IAC1B,eAAe;AACnB;;AAEA;IACI,uBAAuB;IACvB,aAAa;IACb,8BAA8B;IAC9B,sBAAsB;IACtB,WAAW;AACf;;AAEA;IACI,uBAAuB;IACvB,yBAAyB;IACzB,YAAY;IACZ,aAAa;IACb,sBAAsB;IACtB,mBAAmB;AACvB;;AAEA;IACI,uBAAuB;IACvB,WAAW;IACX,YAAY;AAChB;;AAEA;;GAEG;;AAEH;IACI,UAAU;IACV,aAAa;IACb,uBAAuB;AAC3B;;AAEA;IACI,eAAe;IACf,eAAe;IACf,cAAc;IACd,WAAW;IACX,aAAa;IACb,mBAAmB;AACvB;AACA;IACI,aAAa;AACjB;;AAEA;IACI,WAAW;IACX,cAAc;IACd,gBAAgB;IAChB,eAAe;IACf,qBAAqB;AACzB;;AAEA;IACI,yBAAyB;IACzB,UAAU;IACV,oBAAoB;IACpB,gBAAgB;IAChB,eAAe;IACf,kBAAkB;AACtB;AACA;IACI,aAAa;AACjB;AACA;;;GAGG;;AAEH;IACI,yBAAyB;IACzB,UAAU;IACV,mBAAmB;IACnB,qBAAqB;IACrB,gBAAgB;IAChB,aAAa;IACb,kCAAkC;IAClC,8BAA8B;IAC9B,cAAc;AAClB;;AAEA;IACI,YAAY;IACZ,gBAAgB;IAChB,aAAa;IACb,iBAAiB;IACjB,qHAAqH;IACrH,kBAAkB;IAClB,sBAAsB;IACtB,iGAAiG;IACjG,sBAAsB;IACtB,uBAAuB;IACvB,mCAAmC;IACnC,iCAAiC;IACjC,8BAA8B;OAC3B,+BAA+B;OAC/B,2CAA2C;OAC3C,yCAAyC;EAC9C;EACA;IACE;MACE,4BAA4B;IAC9B;IACA;MACE,8BAA8B;IAChC;EACF;EACA;IACE;MACE,4BAA4B;IAC9B;IACA;MACE,8BAA8B;IAChC;EACF","sourcesContent":["body {\n    background-color: #eff1ee;\n    font-family: Arial, Helvetica, sans-serif;\n    display: flex;\n    justify-content: center;\n    padding: 0;\n    margin: 0;\n    height: 100vh;\n}\n\n#root {\n    width: 100%;\n    padding: 1.5em;\n}\n\nnav {\n    display: flex;\n    justify-content: space-between;\n}\n\n.account-links {\n    width: 10rem;\n    display: flex;\n    justify-content: space-evenly;\n}\n#signout-btn:hover {\n    text-decoration: underline;\n    cursor: pointer;\n}\n\n#main-container {\n    border: 1px solid black;\n    display: flex;\n    justify-content: space-between;\n    align-content: stretch;\n    height: 90%;\n}\n\n.container {\n    border: 1px